.App {
    text-align: center;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  .nav-link{
    font-weight: bold;
    color:hwb(0 100% 0% / 0.947)
  }
  .nav-link:hover{
   
    background-color:hwb(0 100% 0% / 0.304);
    color: black;
    transition: 0.9s
   
  }
  @media only screen and (max-width: 600px) {
    
     #first{
        margin-top: 10px;
     }
    
    
  }
  .card:hover{
    color: rgb(0, 7, 6);
    transform: scale(1.01);
 
    
    border-inline-color:black;
    
    transition:0.1s;
    opacity: 0.9;
    overflow: hidden;
   overflow-block:hidden;
   box-shadow: 6px 6px rgb(161, 160, 159);


  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  

 
